@media screen and (max-width: 1024px) {
    .container {
        flex-direction: column;
    }

    .main-content {
        padding: 20px;
    }

    .btn-over-image {
        display: none;
    }

    .content {
        width: 100%; /* ขยาย content ให้เต็มพื้นที่หน้าจอ */
    }

    .home-content {
        display: flex;
        justify-content: space-around;
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        flex-wrap: wrap;
    }

    .home-content div {
        width: 49%;
        margin-top: 5px;
    }

    .home-content div .home-content-i {
        background-color: white;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        padding: 20px;
        box-shadow: 0 2px 3px black;
        position: relative;
        top: 16%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .home-content div p:nth-child(2) {
        margin-top: 0;
        text-align: center;
    }
}

@media screen and (max-width: 850px) {
    .container {
        flex-direction: column;
    }

    /* .text-descript-over-image {
