    } */

    .btn-over-image {
        display: none;
    }

    .btn-over-image button {
        padding: 10px 5px;
        width: 200px;
    }

    .main-content {
        padding: 0;
    }

    .content {
        display: flex;
        flex-direction: column;
        width: 100%;
